<script>
  import {appMode, timeSpans, showSettings, editIdx} from './stores';
  import DobPicker from './DobPicker.svelte';
  import CurrentWeekDetails from './CurrentWeekDetails.svelte';
  import CreateTimeSpan from './CreateTimeSpan.svelte';
  import Calendar from './Calendar.svelte';
  import Settings from './Settings.svelte';
  import EditTimeSpans from './EditTimeSpans.svelte';
  import EditTimeSpan from './EditTimeSpan.svelte';
  import {load, save} from './storageService';

  let hideMobileNotice = load('hideMobileNotice');
  let hidePrivacyNotice = load('hidePrivacyNotice');

  function removeMobileNotice() {
    hideMobileNotice = true;
    save('hideMobileNotice', true);
  }

  function removePrivacyNotice() {
    hidePrivacyNotice = true;
    save('hidePrivacyNotice', true);
  }
</script>

<main>
  <h1 class="title">YOUR LIFE</h1>

  <DobPicker/>

  <div class="create-button-wrapper">
    <div>
      <button on:click|stopPropagation={() => $appMode = 'create-time-span'}>Create time span</button>
    </div>
    {#if $timeSpans.length}
      <div>
        <button on:click|stopPropagation={() => $appMode = 'edit-time-spans'}>Edit time spans</button>
      </div>
    {/if}
  </div>

  <div class="settings-button-wrapper">
    <button on:click|stopPropagation={() => $showSettings = true}>Settings</button>
  </div>

  <Calendar/>
</main>

<CurrentWeekDetails/>

{#if $showSettings}
  <Settings/>
{/if}

{#if $appMode === 'create-time-span'}
  <CreateTimeSpan/>
{/if}

{#if $appMode === 'edit-time-spans'}
  <EditTimeSpans/>
{/if}

{#if $editIdx !== null}
  <EditTimeSpan/>
{/if}

{#if !hidePrivacyNotice}
  <div class="prv-notice">
    This app does not collect or store any personal data.
    Everything will be saved in your browserʼs memory.<br>
    <strong>If you delete your cookies, all will be gone.</strong><br>
    <button on:click={removePrivacyNotice}>Ok</button>
  </div>
{/if}

{#if !hideMobileNotice}
  <div class="mobile-notice">
    While this app should theoretically work on mobile devices,
    you'll have a much better experience on screens with at least 1280 pixels in width.<br>
    <button on:click={removeMobileNotice}>Ok</button>
  </div>
{/if}

